import Vue from 'vue'
import { router } from '@/http'
import store from '@/store'
import auth from './'
import * as constants from '@/constants'

const CHECKIN_URL = constants.API_BASE_URL + '/checkin'
const CHECKOUT_URL = constants.API_BASE_URL + '/checkout'
const LOGIN_URL = constants.API_BASE_URL + '/enter'
const LOGOUT_URL = constants.API_BASE_URL + '/leave'
const REQUEST_URL = constants.API_BASE_URL + '/info'

export default {

  checkin (creds) {
    return new Promise((resolve, reject) => {
      var form = {
        name: creds.username,
        password: creds.password
      }
      Vue.http({
        method: 'post',
        url: CHECKIN_URL,
        data: JSON.stringify(form)
      })
        .then((response) => {
          resolve(response.data)
        })
        .catch((error) => {
          reject(error)
        })
    })
  },

  login (creds, redirect) {
    return new Promise((resolve, reject) => {
      var form = {
        name: creds.username,
        password: creds.password
      }
      return Vue.http({
        method: 'post',
        url: LOGIN_URL,
        data: JSON.stringify(form)
      })
        .then((response) => {
          if (!response.data.hasOwnProperty('failed')) {
            auth.storeToken(response)
            if (redirect) router.push({ name: redirect })
          }
          resolve(response.data)
        })
        .catch((error) => {
          reject(error)
        })
    })
  },

  logout () {
    return new Promise((resolve, reject) => {
      return Vue.http({
        method: 'post',
        url: LOGOUT_URL
      })
        .then((response) => {
          if (!response.data.hasOwnProperty('failed')) {
            store.dispatch('common/clear')
            router.push({ name: 'login' })
          }
          resolve(response.data)
        })
        .catch((error) => {
          reject(error)
        })
    })
  },

  checkout (creds) {
    return new Promise((resolve, reject) => {
      var form = {
        password: creds.password
      }
      return Vue.http({
        method: 'post',
        url: CHECKOUT_URL,
        data: JSON.stringify(form)
      })
        .then((response) => {
          if (!response.data.hasOwnProperty('failed')) {
            store.dispatch('common/clear')
            router.push({ name: 'login' })
          }
          resolve(response.data)
        })
        .catch((error) => {
          reject(error)
        })
    })
  },

  fetch (data, header) {
    return new Promise((resolve, reject) => {
      Vue.http({
        method: 'post',
        url: REQUEST_URL,
        headers: {
          'REQUEST': header[0],
          'ACTION': header[1],
          'Content-Type': data['file'] ? 'multipart/form-data' : 'application/json'
        },
        data: data
      })
        .then((response) => {
          resolve(response.data)
        })
        .catch((error) => {
          reject(error)
        })
    })
  },

  resolveFunc (res) {
    if (!res.hasOwnProperty('failed')) {
      console.log(res)
    } else if (res.failed === 1) {
      console.log('no empty room')
    } else if (res.failed === 2) {
      console.log('user already exists')
    } else if (res.failed === 3) {
      console.log('user does not exist')
    } else if (res.failed === 4) {
      console.log('wrong password')
    } else if (res.failed === 5) {
      console.log('no like-time')
    } else {
      console.log('undefined fail')
    }
  },

  rejectFunc (error) {
    let errorMessage = null
    if (error.response) errorMessage = error.response.status
    else if (error.request) errorMessage = 'no response from server'
    else errorMessage = error.message
    console.log(errorMessage)
  }

}



