import Vue from 'vue'
import { router } from '@/http'
import store from '@/store'
import axios from 'axios'
import * as constants from '@/constants'

const REFRESH_TOKEN_URL = constants.API_BASE_URL + '/refresh'

export default {

  install (Vue, options) {
    Vue.prototype.$auth = Vue.auth = axios.create()

    this.setDefaults()
    this.addInterceptors()
  },

  setDefaults () {
    Vue.auth.defaults.baseURL = constants.API_BASE_URL
    // Vue.auth.defaults.headers.common['Content-Type'] = 'application/json'
  },

  addInterceptors () {
    // Watch for accessToken changes and update our common Auth header.
    store.watch((state) => {
      return state.auth
    }, (auth) => {
      if (!constants.DEBUG) {
        // Vue.auth.defaults.headers.common['Authorization'] = 'Bearer ' + accessToken
        Vue.auth.defaults.headers.common['ACCESS_TOKEN'] = auth.accessToken
        Vue.auth.defaults.headers.common['CLIENT_ID'] = auth.user.id
        // Vue.auth.defaults.transformRequest = [(data, headers) => {
        //   data.access_token = accessToken
        //   return data
        // }]
      }

      if (constants.DEBUG) {
        console.log('token set')
      }
    }, {
      deep: true
    })

    // Intercept the response and refresh (one retry) if invalid token.
    Vue.auth.interceptors.response.use(function (response) {
      if (constants.DEBUG) return Promise.resolve(response)

      if (this.isInvalidToken(response)) {
        return this.refreshToken(response.request)
      }
    }, function (error) {
      return Promise.reject(error)
    })
  },

  isInvalidToken (res) {
    const status = res.response.status
    return status === 401
  },

  refreshToken () {
    return new Promise((resolve, reject) => {
      return axios({
        method: 'post',
        url: REFRESH_TOKEN_URL,
        headers: {
          'Content-Type': 'application/json',
          'REFRESH_TOKEN': store.state.auth.refreshToken
        }
      })
        .then((response) => {
          // console.log(response)
          store.state.auth.accessToken = response.data.result.access_token
          resolve(response.data)
        })
        .catch((error) => {
          // console.log(error)
          reject(error)
          if (this.isInvalidToken(error)) {
            store.dispatch('common/clear')
            router.push({ name: 'login' })
          }
        })
    })
  },

  storeToken (response) {
    const auth = store.state.auth

    auth.isLoggedIn = true
    auth.accessToken = response.data.result.access_token
    auth.refreshToken = response.data.result.refresh_token
    auth.expireTime = 259199
    auth.user.name = response.data.result.name
    auth.user.id = response.data.result.client_id

    store.dispatch('auth/update', auth)
  }
}



